import React, { useState } from 'react';
import IconsSection from './IconsSection';
import DarkMode from './DarkMode';
import BurgerIcon from './UI/BurgerIcon';
import NavSectionBig from './NavSectionBig';
import NavSectionSmall from './NavSectionSmall';
import useBodyScrollLock from './hooks/useBodyScrollLock';

const Navbar = () => {
  const [opened, setOpened] = useState(false);
  const [isLocked, toggle] = useBodyScrollLock();

  const menuHandler = (e) => {
    setOpened(!opened);
    toggle();
  };

  const links = ['Skills', 'Projects', 'Contact'];

  return (
    <>
      <div className=" absolute  left-2/4  -translate-x-2/4 h-[90px] flex justify-evenly container xl  mx-auto items-center  text-indigo-950 text-2xl font-poppins max-lg:relative max-lg:px-10 max-lg:justify-between max-lg:mx-0  z-50">
        <NavSectionBig links={links} />
        <div className="max-lg:hidden">
          <IconsSection />
        </div>
        <DarkMode />
        <BurgerIcon opened={opened} menuHandler={menuHandler} />
      </div>
      <NavSectionSmall
        opened={opened}
        links={links}
        menuHandler={menuHandler}
      />
    </>
  );
};

export default Navbar;
